.best {
  display: flex;
  width: 100%;
  justify-content: space-between;
  &__item {
    padding-top: 41px;
    width: 32%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #f8f9fa;
    &__title {
      font-family: "Readex";
      font-style: normal;
      font-weight: 400;
      font-size: 28px;
      line-height: 35px;
      text-align: center;
      color: #000000;
    }
    &__subtitle {
      font-family: "Readex";
      font-style: normal;
      font-weight: 400;
      font-size: 13px;
      line-height: 17px;
      text-align: center;
      color: #000000;
    }
  }
  &__btn {
    width: 190px;
    margin-bottom: 30px;
  }
}
