.best {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.best__item {
  padding-top: 41px;
  width: 32%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f8f9fa;
}
.best__item__title {
  font-family: "Readex";
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 35px;
  text-align: center;
  color: #000000;
}
.best__item__subtitle {
  font-family: "Readex";
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 17px;
  text-align: center;
  color: #000000;
}
.best__btn {
  width: 190px;
  margin-bottom: 30px;
}